import React from 'react'
import Link from 'next/link'
import NavStyles from './styles/NavStyles'
import User from './User'
import Signout from './Signout'
import { Mutation } from 'react-apollo'
import  { TOGGLE_CART_MUTATIOM } from './Cart'
import CartCount from './CardCount';
const Nav = () => (
  <User>
      {({data: { me }}) => (
        <NavStyles>
        <Link href="/items">
          <a>Shop</a>
        </Link>
        {me && (
        <>
          <Link href="/sell">
            <a>Sell</a>
          </Link>
          <Link href="/orders">
            <a>Orders</a>
          </Link>
          <Link href="/me">
            <a>Account</a>
          </Link>
          <Signout />
          <Mutation mutation={TOGGLE_CART_MUTATIOM}>
            {(toggleCart) => (
              <button onClick={toggleCart}>
                My Cart
                <CartCount count={me.cart.length}></CartCount>
              </button>
            )}
          </Mutation>
          
        </> 
        )}
        {!me && (
          <Link href="/signup">
            <a>Sign Up</a>
          </Link>
        )}
      </NavStyles>
      )}
    </User>
)

export default Nav